@import url(https://fonts.googleapis.com/css?family=Nobile:400italic,700italic);
@import url(https://fonts.googleapis.com/css?family=Dancing+Script);
* {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
}
body {
  background: #e5e5e5;
  background-image: url("/gray-floral.png");
  padding: 50px;
}

#card-front {
  color: #ffdfdf;
}

#card,
#card-front,
#card-inside {
  height: 480px;
}

.mt0 {
  margin-top: 0 !important;
}
.mb0 {
  margin-bottom: 0 !important;
}

.wrap {
  padding: 1.5em 2.5em;
  height: 100%;
}
#card-front,
#card-inside {
  width: 50%;
  -webkit-box-shadow: 2px 2px 30px rgba(0, 0, 0, 0.25),
    0 0 1px rgba(0, 0, 0, 0.5);
  -moz-box-shadow: 2px 2px 30px rgba(0, 0, 0, 0.25), 0 0 1px rgba(0, 0, 0, 0.5);
  box-shadow: 2px 2px 30px rgba(0, 0, 0, 0.25), 0 0 1px rgba(0, 0, 0, 0.5);
  position: absolute;
  left: 50%;
}

#card-inside .wrap {
  background: #ecefff;
  -webkit-box-shadow: inset 2px 0 1px rgba(0, 0, 0, 0.05);
  -moz-box-shadow: inset 2px 0 1px rgba(0, 0, 0, 0.05);
  box-shadow: inset 2px 0 1px rgba(0, 0, 0, 0.05);
}
#card {
  max-width: 960px;
  margin: 0 auto;
  transform-style: preserve-3d;
  -moz-transform-style: preserve-3d;
  -webkit-transform-style: preserve-3d;
  perspective: 5000px;
  -moz-perspective: 5000px;
  -webkit-perspective: 5000px;
  position: relative;
  padding-top: 10%;
}

#card h1 {
  text-align: center;
  font-family: "Nobile", sans-serif;
  font-style: italic;
  font-size: 70px;
  text-shadow: 4px 4px 0px rgba(0, 0, 0, 0.15), 1px 1px 0 #6c6593,
    2px 2px 0 #6c6593, 3px 3px 0 #6c6593;
  color: #ecefff;
}
#card-inside {
  font-size: 12px;
  line-height: 2;
  font-family: "Nobile";
  color: #331717;
  font-style: italic;
}

p {
  margin-top: 1em;
}

p:first-child {
  margin-top: 0;
}

p.signed {
  margin-top: 1.5em;
  text-align: center;
  font-family: "Dancing Script", sans-serif;
  font-size: 1.5em;
}

#card-front {
  background-color: #ff5555;
  background-image: linear-gradient(to bottom, #89a0e2 0%, #3c417d 100%);
  background-image: -moz-linear-gradient(to bottom, #89a0e2 0%, #3c417d 100%);
  background-image: -webkit-linear-gradient(
    to bottom,
    #89a0e2 0%,
    #3c417d 100%
  );
  transform-origin: left;
  -moz-transform-origin: left;
  -webkit-transform-origin: left;
  transition: transform 1s linear;
  -moz-transition: -moz-transform 1s linear;
  -webkit-transition: -webkit-transform 1s linear;
  position: relative;
}

#card-front .wrap {
  transition: background 1s linear;
  -moz-transition: background 1s linear;
  -webkit-transition: background 1s linear;
}

#card-front button {
  position: absolute;
  bottom: 1em;
  right: -12px;
  background: #201b48;
  color: #fff;
  font-family: "Nobile", sans-serif;
  font-style: italic;
  font-weight: bold;
  font-size: 1.5em;
  padding: 0.5em;
  border: none;
  cursor: pointer;
  box-shadow: 2px 2px 3px rgba(0, 0, 0, 0.25), 0 0 1px rgba(0, 0, 0, 0.4);
  -moz-box-shadow: 2px 2px 3px rgba(0, 0, 0, 0.25), 0 0 1px rgba(0, 0, 0, 0.4);
  -webkit-box-shadow: 2px 2px 3px rgba(0, 0, 0, 0.25),
    0 0 1px rgba(0, 0, 0, 0.4);
}

#card-front button:hover,
#card-front button:focus {
  background: #89a0e2;
}

#close {
  display: none;
}

#card.open-fully #close,
#card-open-half #close {
  display: inline;
}

#card.open-fully #open {
  display: none;
}

#card.open-half #card-front,
#card.close-half #card-front {
  transform: rotateY(-90deg);
  -moz-transform: rotateY(-90deg);
  -webkit-transform: rotateY(-90deg);
}
#card.open-half #card-front .wrap {
  background-color: rgba(0, 0, 0, 0.5);
}

#card.open-fully #card-front,
#card.close-half #card-front {
  background: #ecefff;
}

#card.open-fully #card-front {
  transform: rotateY(-180deg);
  -moz-transform: rotateY(-180deg);
  -webkit-transform: rotateY(-180deg);
}

#card.open-fully #card-front .wrap {
  background-color: rgba(0, 0, 0, 0);
}

#card.open-fully #card-front .wrap *,
#card.close-half #card-front .wrap * {
  display: none;
}

footer {
  max-width: 500px;
  margin: 40px auto;
  font-family: "Nobile", sans-serif;
  font-size: 14px;
  line-height: 1.6;
  color: #888;
  text-align: center;
}
